.App {
    height: 100%;
    display: flex;
    flex-direction: column;
}

html,
body {
    height: 100%;
}

.content {
    flex: 1 0 auto;
    text-align: center;
}
.footer {
    flex-shrink: 0;
    padding: 0.5em;
}

.button {
    box-shadow: inset 0px 1px 0px 0px #ffffff;
    background: linear-gradient(to bottom, #ededed 5%, #dfdfdf 100%);
    background-color: #ededed;
    border-radius: 6px;
    border: 1px solid #dcdcdc;
    display: inline-block;
    cursor: pointer;
    color: #777777;
    font-family: Arial;
    font-size: 15px;
    font-weight: bold;
    padding: 6px 24px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #ffffff;
}

.icon {
    margin: 0 5px 0 5px;
    width: 16px;
    height: 16px;
}
.icon,
.button * {
    vertical-align: middle;
    display: inline-block;
}
.button:hover {
    background: linear-gradient(to bottom, #dfdfdf 5%, #ededed 100%);
    background-color: #dfdfdf;
}
.button:active {
    position: relative;
    top: 1px;
}
.button:disabled {
    background: none;
    background-color: #ffffff;
    cursor: not-allowed;
}
.button:disabled:hover {
    background: none;
    background-color: #ffffff;
    cursor: not-allowed;
}
.button:disabled:active {
    position: relative;
    top: 0px;
}

.me-3 {
    margin-right: 1em;
}

.ms-3 {
    margin-left: 1em;
}

.footertext {
    font-size: xx-small;
    font-style: italic;
    text-align: right;
}
